html, body, #root {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dropzone {
  margin-bottom: .5em;
}

.btn-toolbar > button {
  margin-left: .5em;
}

.page-container {
  padding: 24px;
}

.loading {
  position: relative;
  width: 100%;
  height: 100%;
}

.loading > div {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 4rem;
  height: 4rem;
  margin-top: -2rem;
  margin-left: -2rem;
  border-width: .5em;
}

.card-container {
  width: 100%;
  height: 100%;
}

.circle-card,
.product-card {
  width: 100%;
  height: 100%;
  padding: 24px;
}

.circle-card  > .card,
.product-card > .card {
  width: 100%;
  height: 100%;
}

.circle-card  .card-header,
.product-card .card-header {
  padding: .75rem .75rem;
  display: flex;
}

.circle-card  .spacer,
.product-card .spacer {
  flex-grow: 1;
}

.circle-card  .card-header > .badge,
.product-card .card-header > .badge {
  margin-right: .25rem;
  font-size: 100%;
}


.circle-card  img,
.product-card img {
  height: 200px;
}

.circle-card  .card-body,
.product-card .card-body {
  overflow-y: scroll;
  height: calc(100% - 200px);
}

.circle-card  .form-label,
.product-card .form-label {
  font-size: 75%;
  margin-bottom: 0;
  border-bottom: 3px solid lightgray;
  width: 100%; 
}

.circle-card  .form-group,
.product-card .form-group {
  margin-bottom: 0;
}

.btn-hidden {
  display: none;
}

.circle-prev, .circle-next,
.product-prev, .product-next {
  position: absolute;
}
.circle-prev, .circle-next {
  height: calc(100% - 75px * 2);
  min-width: 75px;
  width: 10%;
  margin-top: 75px;
}
.circle-prev {
  left: 0;
  top: 0;
}
.circle-next {
  right: 0;
  top: 0;
}
.circle-prev > svg {
  margin-left: -32px;
}
.circle-next > svg {
  margin-right: -32px;
}
.product-prev, .product-next {
  width: calc(100% - 75px * 2);
  min-height: 75px;
  height: 10%;
  margin-left: 75px;
}
.product-prev {
  left: 0;
  top: 0;
  width: calc(100% - 100px * 2);
  margin-left: 100px;
}
.product-next {
  left: 0;
  bottom: 0;
}
.product-prev > svg {
  margin-top: -48px;
}
.product-next > svg {
  margin-bottom: -24px;
}

.card-menu-btn {
  position: absolute;
  left: 8px;
  top:  8px;
}

.fav-list .circle-space, .fav-list .circle-space > button {
  word-break: keep-all;
  white-space: nowrap;
  font-size: 90%;
}
.fav-list .circle-name {
  font-size: 90%;
}
.fav-list .product-name {
  font-size: 90%;
}
.fav-list .product-price {
  word-break: keep-all;
  white-space: nowrap;
  text-align: right;
  font-size: 90%;
}
